


[class*="Panel-"] {
  display: flex;
}

.Panel-1-Nav {
  height: 100vh;
  background-color: rgb(193, 201, 195);
  margin: 1%;
  border-radius: 8px;
  width: 30vw;
  font-size: 30px;
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-1-Nav > *:first-child {
  padding: 5% 10% 5% 10%;
}

.Panel-2-Nav-User {
  flex-direction: column;
  margin: 1%;
  border-radius: 8px;
  background-color: rgb(111, 243, 144);
}

.Panel-0 {
  background-color: white;
  height: 100%;
  width: 100%;
}

.Navbar {
  background-color: rgb(193, 201, 195);
  width: 20vw;
  font-size: 30px;
}

.emphasize-dark {
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.emphasize-light {
  box-shadow: 0 0 0 10px rgba(255,255,255,.25);
}

.emphasize-inset {
  box-shadow: inset 0 0 7px 4px rgba(255,255,255,.5);
}

.emphasize-border {
  box-shadow: inset 0 0 0 7px rgba(255,255,255,.5);
}